import './css/App.css';
import Email from './assets/email.svg';
import GitHub from './assets/github.svg';
import LinkedIn from './assets/linkedin.svg';
import { useContext, createContext, useState, useRef } from 'react';
import { Routes, Route, Link } from 'react-router-dom';
import Home from './routes/home';
import Budget from './routes/budget/budget';
import Errands from './routes/errands';
import Fuel from './routes/fuel/fuel';
import sources from './data/fuel.json';
import plans from './data/meals.json';
import resume from './data/resume.json';
import SignIn from './routes/sign-in';
import error from './assets/404 error.svg';

export const MealContext = createContext();

function App() {

  // INTRODUCTION
  
  const intro = {
    type: "intro",
    title: "Hi,",
    body: ["I'm Peter, lover of cats, art & computers.", "Exploring different ideas is where my attention gravitates.", "Venturing through new territories of interactivity to enhance life for greater good.", "Creating imaginative functions in fun forms."]
  }
  const [contentType, setContentType] = useState(intro);

  const signIn = {
    type: "signIn",
    title: "Sign IN",
    body: [
    <input id="username" className="signIn-input"/>,
    <input id="password" className="signIn-input"/>,
    <button>Sign IN</button>
  ]
  }

  const codingSkills = {
    type: "list",
    title: "Skills",
    body: [
        "HTML",
        "CSS",
        "JavaScript",
        "React",
        "CLI",
        "Python",
        "SQL",
        "Git",
        "NodeJS"
    ]
  }

  const contactDetails = {
    type: "link",
    title: "Contact",
    body: [
      <a className="mailtoui" href="mailto:peterpczerniak@outlook.com" target="_blank" rel="noreferrer noopener"><img className="contact-logo" alt="email symbol" src={Email}/></a>,
      <a className="contact-link" href="https://www.linkedin.com/in/peter-czerniak-48030b83/" target="_blank"><img className="contact-logo" alt="LinkedIn link" src={LinkedIn}/></a>,
      <a className="contact-link" href="https://github.com/peeczi/peeczi.github.io" target="_blank"><img className="contact-logo" alt="GitHub" src={GitHub}/></a>
    ]
  };

  const projects = {
    type: "list",
    title: "Projects",
    body: [
      <Link className="link-item" to="/fuel">Fuel Up With</Link>,
      <Link className="link-item" to="/errands">Airin' Out Errands</Link>,
      <Link className="link-item" to="/budget">No Budgin' with The Budgetin'</Link>
    ]
  }

  const homeContent = (event) => {
    const contentSelect = event.target.id;
    switch(contentSelect) {
      case 'profile-pic':
        setContentType(intro);
        break;
      case 'signIn':
        setContentType(signIn);
        break;
      case 'skills':
        setContentType(codingSkills);
        break;
      case 'projects':
        setContentType(projects);
        break;
      case 'resume':
        setContentType(resume);
        break;
      case 'contact':
        setContentType(contactDetails);
        break;
      default:
        console.error("Switch case error");
    }
  }

  // NO BUDGIN' WITH MY BUDGETIN'
  const [budget, setBudget] = useState();
  const [showBudgetInfo, setShowBudgetInfo] = useState(false);
  const [showExpenseInfo, setShowExpenseInfo] = useState(false);
  const [showAllCategories, setShowAllCategories] = useState(false);
  const [showFocusCategory, setShowFocusCategory] = useState(false);
  const [focusCategory, setFocusCategory] = useState();

  // expenses
  const expenseItemRef = useRef();
  const expenseAmountRef = useRef();
  const expenseCategoryRef = useRef();

  // calculator
  const [showCalculator, setShowCalculator] = useState(false);
  const [calculation, setCalculation] = useState();
  const [dividend, setDividend] = useState();
  const [divisor, setDivisor] = useState();
  const [operator, setOperator] = useState();

  const [expense, setExpense] = useState({
    "home": [],
    "school": [],
    "medical": [],
    "work": [],
    "car": [],
    "leisure": [],
    "other": [],
    "test": []
  });

  const budgetEnter = (event) => {
      const budgetAmount = event.target.value;
      if (budgetAmount > 0) {
        setBudget(budgetAmount);
        event.target.value = null
      }
  }

  // add expense entry
  const handleExpenseSubmit = (event) => {
    event.preventDefault();
    
    const expenseCategory = String(expenseCategoryRef.current.value);

    let xpItem = expenseItemRef.current.value;
    let xpAmount = Number(expenseAmountRef.current.value);

    if (xpItem !== '' && xpAmount > 0){
      // SET EXPENSE ACCORDING TO
      if (Object.entries(expense[expenseCategory]).length === 0) {
          setExpense(prevExpense => ({
            ...prevExpense,
            [expenseCategory]:
            [{[xpItem]:xpAmount}]
          }))
        
      } else {
        setExpense(prevExpense => ({
        ...prevExpense,
        [expenseCategory]:  [
          ...prevExpense[expenseCategory],
            {[xpItem]:xpAmount}
          ]
      }))}
      setShowExpenseInfo(true);
      
      if (showAllCategories === false && showFocusCategory === false && focusCategory === undefined){
        setShowFocusCategory(true);
        setFocusCategory(expenseCategory);
      }
    }
  }

  function calculateCategorySubtotal(expenseCategory) {
    if (Object.entries(expenseCategory).length > 0){
      let subtotal = 0;
      Object.values(expenseCategory).map((expenseCategoryExpense) => {
        subtotal += Number(Object.values(expenseCategoryExpense));
        return subtotal;
      })
      return subtotal;
    } 
  }

  function calculateExpenseTotal() {
    let expenseTotal = 0;
    Object.entries(expense).map((expenseCategory) => {
      const expenseTotalCalc = Object.values(expenseCategory[1]).map((category) => {
        expenseTotal += Number(Object.values(category));
        return expenseTotal;
      })
      return expenseTotalCalc[expenseTotalCalc.length-1];
    });
      return expenseTotal;
  }
  
  const expenseTotal = calculateExpenseTotal();

    // show calculator button state
  function showCALC() {
    setShowCalculator(prevState => !prevState);
  }

  // calculator functions
  function calculate(event) {
    const buttonId = event.target.id;
    const buttonValue = event.target.value;
    const isButtonNumber = Number(event.target.id);
    const buttonsClassName = event.target.className;

    // 6 / 2 = 3
    // dividend = 6
    // divisor = 2
    // quotient = 3

    function operate() {
      if (calculation === undefined) {
        if (operator === "+") {
            setCalculation(Number(dividend) + Number(divisor));
        } else if (operator === "-") {
          setCalculation(Number(dividend) - Number(divisor));
        } else if (operator === "*") {
          setCalculation(Number(dividend) * Number(divisor));
        } else if (operator === "/") {
          setCalculation(Number(dividend) / Number(divisor));
        } else if (operator === "%") {
          setCalculation(Math.round((Number(dividend) / Number(divisor) * 100)));
        }
      } else if (calculation !== undefined) {
        if (operator === "+") {
          setCalculation(calculation + Number(divisor));
        } else if (operator === "-") {
          setCalculation(calculation - Number(divisor));
        } else if (operator === "*") {
          setCalculation(calculation * Number(divisor));
        } else if (operator === "/") {
          setCalculation(calculation / Number(divisor));
        } 
      }
    }

    // equals button has been pressed
    if (buttonId === "=") {
      operate();
    }

    // AC button
    if (buttonId === "AC") {
      setCalculation(undefined);
      setDividend(undefined);
      setDivisor(undefined);
      setOperator(undefined);
      return;
    }

    // DIVIDEND
    if (operator === undefined && divisor === undefined && !buttonsClassName.includes("operator")) {

      // new calculation
     if (dividend === undefined && !buttonsClassName.includes("operator") && operator === undefined) {
      setDividend(buttonValue);

        // if entering number < 1
      } else if (dividend === "0" && buttonId === ".") {
        setDividend(prevDividend => prevDividend + buttonValue);

        // dividend is NOT a decimal
      } else if (dividend !== "0" && dividend !== "0." && !buttonsClassName.includes("operator") && operator === undefined && divisor === undefined) {
        setDividend(prevDividend => prevDividend + buttonValue);
      } 
    }

    // OPERATOR
    if (dividend !== undefined && divisor === undefined && buttonsClassName.includes("operator")) {
      if (buttonsClassName.includes("operator") && divisor === undefined) {
        setOperator(buttonValue);
      }  
    }

    // DIVISOR
    if (dividend !== undefined && operator !== undefined && !buttonsClassName.includes("operator")) {
      
      // divisor setting
      if (operator !== undefined && !buttonId.includes("operator") && divisor === undefined && buttonId !== "=") {
        setDivisor(buttonValue);
      } else if (operator !== undefined && !buttonId.includes("operator") && divisor !== undefined && buttonId !== "=") {
        setDivisor(prevDivisor => prevDivisor + buttonValue);
      }
    }

    // CALCULATION
    if (divisor !== undefined) {
      if (calculation !== undefined && buttonsClassName.includes("operator")) {
        setDividend(calculation);
        setOperator(buttonValue);
        setDivisor(undefined);
      }
    }
  }

  function deleteCalculate() {
    let lastInput;
    if (dividend !== undefined && operator === undefined)  {
      if (Object.values(dividend).length === 1) {
        setDividend(undefined);
      } else {
        lastInput = dividend.slice(0, -1);
        setDividend(lastInput);
      }
     
    } else if (operator !== undefined && divisor === undefined) {
     setOperator(undefined);

    } else if (divisor !== undefined && calculation === undefined) {
      if (Object.values(divisor).length === 1) {
        setDivisor(undefined);
      } else {
        lastInput = divisor.slice(0, -1);
        setDivisor(lastInput);
      }

    } else if (calculation !== undefined) {
     setCalculation(undefined);
    }
  }

  function expandCategoryExpenses(event) {
    const expenseCategoryExpand = event.target.id;
    setShowFocusCategory(prevState => !prevState);
    setShowAllCategories(prevState => !prevState);
    setFocusCategory(expenseCategoryExpand);
  }

  // FUEL UP WITH
  const foodSources = sources.energy;
  const mealPlans = plans.plans;

  // cuisines, meal types & diet
  const [planRoute, setPlanRoute] = useState();

  // ingredients, food formats & restrictions/ goals
  const [mealPlanData, setMealPlanData] = useState();

  // user entered data to help plan their meals
  const [userData, setUserData] = useState({
    favorites:[], 
    restrictions:[], 
    metrics:{calories:0, fat:0, sugar:0, protein:0, carbohydrates:0}
  })

  // default content; quick description of inputs
  const fuelIntro = {"get started": {"ingredient": "Search meals by ingredient","random": "Generate random meal", "minerals": "Overview of crucial minerals/ vitamins for proper health", "meal plans": "Selection of helpful paths to plan meals"}}

  // display selected path (random, minerals, plan) in content area
  const [contentPath, setContentPath] = useState()
  
  // final message
  const loves = ["love", "amor", "liefde", "dashuri", "ljubav", "armastus", "loloma", "pag-ibig", "rakkaus", "amour", "liebe", "szeretet", "amore", "salang", "aiqing", "milosc", "dragoste", "karlek", "ai cheng", "rirhandzu"]
  const [love, setLove] = useState("love");

  // MEALDB RANDOM MEAL
  const randomMealUrl = "https://www.themealdb.com/api/json/v1/1/random.php";

  // search by ingredient
  const changeIngredientTarget = (event) => {
    const ingredientTarget = event.target.value;
    if (event.key === 'Enter') {
      setLove("...amor");
  
      if (ingredientTarget !== undefined){
        fetch(`https://www.themealdb.com/api/json/v1/1/filter.php?i=${ingredientTarget}`)
        .then(response => response.json())
        .then(response => {
          if (response.meals === null) {
            fetch(`https://www.themealdb.com/api/json/v1/1/filter.php?i=${ingredientTarget}s`)
            .then(response => response.json())
            .then (response => {
              setContentPath(null)
              setContentPath({"ingredient":response.meals});
            })
          }
          setContentPath(null)
          setMealPlanData(undefined);
          setPlanRoute(undefined);
          setContentPath({"ingredient":response.meals});
          
        })
        .catch(err => {
          console.error(err);
          setContentPath(null);
          setMealPlanData(undefined);
          setPlanRoute(undefined);
          setContentPath({"ingredient":[{strMeal: "Ingredient not found in database"}]})
        });
      }
      event.target.value = ""
    }
  }

  // add meal ideas to user's personal list of favorites
  function addFavorite(event){
    const favorite = event.target.id

    if (!userData.favorites.includes(favorite)) {
      setUserData((prevRestrict => ({
        ...prevRestrict, 
        favorites:
        [...prevRestrict.favorites, favorite]
      })))
    }
    event.target.value = ""
  }

  function getFavorites() {
    setContentPath({"favorites":userData.favorites})
    setLove(loves[Math.floor(Math.random() * loves.length)])
  }

  // restriction to diet
  function addRestriction(event) {
    const restriction = event.target.value
    const restrictionType = event.target.name

    if (event.key === "Enter") {
      if (restrictionType === "restrictions") {
        setUserData((prevRestrict => ({
          ...prevRestrict, 
          [restrictionType]:
          [...prevRestrict[restrictionType], restriction]
        })))
      }
      event.target.value = ""
    }
  }

  // metric goal input for diet
  function addMetricGoal(event) {
    let metricAmount = event.target.value
    const metricType = event.target.name

    if (!isNaN(Number.parseInt(metricAmount))) {  
      metricAmount = Number.parseInt(metricAmount)
      setUserData((prevRestrict => ({
        ...prevRestrict, 
        metrics:{
        ...prevRestrict.metrics,
          [metricType]:metricAmount}
      })))
    }
  }

  const fetchRandoMeal = () => {
    fetch(randomMealUrl)
    .then(response => response.json())
    .then(response => {
      setLove("...milosc");
      setMealPlanData(undefined);
      setPlanRoute(undefined);
      setContentPath({"randoMeal":response.meals[0]})
      return response.meals[0]
    })
    .catch(err => console.error(err));
  }

  // three button inputs for meal ideas
  function changeFuelContent(event) {
    const content = event.target.id
    setPlanRoute(undefined);
    setMealPlanData(undefined);
    
    switch(content) {
      case "randoMeal":
        setContentPath({[content]:fetchRandoMeal()})
        return
      case "minerals":
        setContentPath({[content]:sources})
        return
      case "mealPlans":
        setContentPath({[content]:mealPlans})
        return
      default:
        setContentPath(null);
        break;
    }
  }

  // three paths user takes to plan a meal: cuisine, type, dietary needs
  function fetchPlanRoute(event) {
    const planChoice = event.target.id;
    setMealPlanData(undefined);

    switch(planChoice) {
      case "cuisine":
        setPlanRoute(mealPlans[0]["cuisine"]);
        return mealPlans[0]
      case "type":
        setPlanRoute(mealPlans[1]["type"]);
        return mealPlans[1]
      case "dietary needs":
        setPlanRoute(mealPlans[2]["dietary needs"]);
        return mealPlans[2]
      default:
        setPlanRoute(undefined);
        break;
    }
  }

  // show meal selections based on type eg. soup, sandwich, patty
  function showData(event) {
    const selected = event.target.id;
    const selectedIndex = selected.slice(-selected.length-1, 1);
    const selectedPath = selected.slice(1);

    if (selectedPath === "restrictions") {
      setMealPlanData(selectedPath)
      return
    }

    if (selectedPath === "metrics") {
      console.log(`METRICSSS`)
      setMealPlanData(selectedPath)
      return
    }

    const dataDump = planRoute[selectedIndex][selectedPath].map((data) => {
      return data
    })

    setMealPlanData(dataDump);
  }

  // ERRANDS
  const [errands, setErrands] = useState({
    "home": [],
    "school": [],
    "medical": [],
    "work": [],
    "car": [],
    "leisure": [],
    "other": []
  });
  const errandCategories = ["home", "school", "medical", "work", "car", "leisure", "other"];
  const errandTaskInput = useRef();
  const errandDueDateInput = useRef();
  const [scrollCount, setScrollCount] = useState(0);
  const [errandCategory, setErrandCategory] = useState(errandCategories[0]);

  function categoryChange(){
    console.log(`WheelEvent.deltaY ${WheelEvent.deltaY}`)
    let scroll
    if (scrollCount === 6) {
      setScrollCount(0);
    } else {
      scroll = scrollCount + 1;
      setScrollCount(scroll)
    }
    setErrandCategory(errandCategories[scrollCount]);
  }

  function getErrandCategory() {
    const categoryIndex = errandCategories.map(category => {
      if (category === errandCategory) {
        return errandCategories.indexOf(category)
      }
    })

    return categoryIndex
  }

  function categoryChangeDown(){
    console.log(`getErrandCategory() ${getErrandCategory()}`)
    console.log(`WheelEvent.deltaY ${WheelEvent.deltaY}`)
    let scroll
    if (scrollCount === 0) {
      setScrollCount(6);
    } else {
      scroll = scrollCount - 1;
      setScrollCount(scroll)
    }
    setErrandCategory(errandCategories[scrollCount]);
  }

  function categoryChangeUp(){
    console.log(`getErrandCategory() ${getErrandCategory()}`)
    console.log(`WheelEvent.deltaY ${WheelEvent.deltaY}`)
    let scroll
    if (scrollCount === 6) {
      setScrollCount(0);
    } else {
      scroll = scrollCount + 1;
      setScrollCount(scroll)
    }
    setErrandCategory(errandCategories[scrollCount]);
  }

  function onErrandUpdate(event) {
    event.preventDefault();

    let errandTaskItem = errandTaskInput.current.value;
    let errandDueDate = errandDueDateInput.current.value;

    if (errandTaskItem !== "" && errandDueDate !== "") {
      if (errands[errandCategory].length === 0) {
        setErrands(prevErrands => ({...prevErrands, [errandCategory]:[{[errandTaskItem]:errandDueDate}]}))
        errandTaskInput.current.value = null
        return
      }
      if (errands[errandCategory].length >= 1) {
        setErrands(prevErrands => ({ 
          ...prevErrands,
          [errandCategory]: [
            ...prevErrands[errandCategory],
            {[errandTaskItem]:errandDueDate}
          ]
        }))
        errandTaskInput.current.value = null
        return
      }
    }
  }
  

  return (
    <Routes>
      <Route 
        path="/" 
        element={
        <Home
          onChange={homeContent}
          contentType={contentType}
          resume={resume}
        />}
      />
      <Route 
        path="signIn" 
        element={
        <SignIn
        />}
      />
      <Route 
        path="budget" 
        element={
        <Budget 
          budget={budget}
          expense={expense}
          // bills={bills}
          expenseTotal={expenseTotal}
          expenseItemRef={expenseItemRef}
          expenseAmountRef={expenseAmountRef}
          expenseCategoryRef={expenseCategoryRef}
          budgetEnter={budgetEnter}
          showBudgetInfo={showBudgetInfo}
          showExpenseInfo={showExpenseInfo}
          calculateCategorySubtotal={calculateCategorySubtotal}
          calculateExpenseTotal={calculateExpenseTotal}
          expandCategoryExpenses={expandCategoryExpenses}
          showAllCategories={showAllCategories}
          showFocusCategory={showFocusCategory}
          focusCategory={focusCategory}
          // handleBudgetSubmit={handleBudgetSubmit}
          handleExpenseSubmit={handleExpenseSubmit}
          showCALC={showCALC}
          showCalculator={showCalculator}
          calculate={calculate}
          calculation={calculation}
          dividend={dividend}
          divisor={divisor}
          operator={operator}
          deleteCalculate={deleteCalculate}
        />}
      />
      <Route 
        path="errands" 
        element={
        <Errands 
          errands={errands}
          errandCategory={errandCategory}
          categoryChange={categoryChange}
          categoryChangeUp={categoryChangeUp}
          categoryChangeDown={categoryChangeDown}
          onErrandUpdate={onErrandUpdate}
          errandTaskInput={errandTaskInput}
          errandDueDateInput={errandDueDateInput}
          />} 
      />
      <Route 
        path="fuel" 
        element={
        <Fuel 
          changeIngredientTarget={changeIngredientTarget} 
          changeFuelContent={changeFuelContent}
          contentPath={contentPath}
          love={love} 
          sources={foodSources}
          mealPlans={mealPlans}
          showData={showData}
          fetchPlanRoute={fetchPlanRoute}
          planRoute={planRoute}
          setPlanRoute={setPlanRoute}
          setMealPlanData={setMealPlanData}
          mealPlanData={mealPlanData}
          setContentPath={setContentPath}
          fetchRandoMeal={fetchRandoMeal}
          userData={userData}
          addRestriction={addRestriction}
          addMetricGoal={addMetricGoal}
          addFavorite={addFavorite}
          getFavorites={getFavorites}
        />
        } 
      />
    </Routes>
  );
}

export default App;
