import React, { useRef } from 'react';
import Main from '../components/main.component';
import '../css/errands.styles.css';
import scroll from '../assets/scroll-up-down.svg';
import add from '../assets/add.svg';
import '../css/clouds.styles.css';
import Errand from './errand';

function Errands({ errands, errandCategory, errandDueDateInput, errandTaskInput, categoryChange, categoryChangeUp, categoryChangeDown, onErrandUpdate}) {
  const dateFormatRef = useRef();

  function changeDateFormat(event) {
    const errandEntryDate = event.target.id
    console.log(`errandEntryDate ${errandEntryDate}`);
    console.log(`event.target.className ${event.target.className}`)
    
    const months = ["phony", "january", "february", "march", "april", "may", "june", "july", "august", "september", "october", "november", "december"]
    const monthNameErrandDue = months[Number.parseInt(errandEntryDate[5] + errandEntryDate[6])]
    const monthErrandDue = errandEntryDate[5] + errandEntryDate[6]
    const dayErrandDue = errandEntryDate[8] + errandEntryDate[9]
    const yearErrandDue = errandEntryDate[0] + errandEntryDate[1] + errandEntryDate[2] + errandEntryDate[3]

    const namedDateFormat = monthNameErrandDue + " " + dayErrandDue + ", " + yearErrandDue
    event.target.value = namedDateFormat

    return namedDateFormat
    
  }

  function deleteErrand(event) {
    const errand = event.target.id
    // delete errands[errandCategory][errand]
  }

  return (
    <div className="errands">
      <div id="errands-header">
        <Main />
        <h1 id="errands-title">Airin My Errands</h1>
      </div>
      <div className="inp">
        <div id="errand-category-picker">
          <div id="cloud">
            <div id="cloud-main-1"></div>
            <div id="cloud-main-2"></div>
            <div id="cloud-main-3"></div>
            <div id="cloud-main-4"></div>
          </div>
          <p 
            id="errand-category" 
            onWheel={categoryChange}
            // onMouseUp={categoryChangeUp}
            // onMouseDown={categoryChangeDown}
          >
            {errandCategory}
          </p>
          <img
            id="scroll-icon"
            src={scroll}
            alt="scroll icon"
          />
        </div>

        <form id="errand-input" onSubmit={onErrandUpdate}>
          <input
            type="text"
            className="errand-input"
            placeholder="errand"
            id="errand-task"
            ref={errandTaskInput}
          />
        
          <input 
            type="date" 
            className="errand-input" 
            id="task-due-date"
            ref={errandDueDateInput}
          />
          <button type="submit" id="add-errand-button">
            <img
              id="add-icon"
              src={add}
              alt="add icon"
            />
          </button>
        </form>
      </div>

      { errands && 
      <div className="outp">
        {Object.entries(errands[errandCategory]).map((errand, index) => {
          return <div key={index} className={`errand-entry${index % 2 ? 'even' : 'odd'}`}>
                  <div className="errand-task-print">
                    <Errand
                      id={Object.keys(errand[1])}
                      errands={Object.keys(errand[1])}
                      onClick={deleteErrand}
                    />
                  </div>
                  
                  <div className="errand-value-print">
                    <Errand
                      id={Object.values(errand[1])} 
                      errands={Object.values(errand[1])} 
                      onClick={changeDateFormat}
                    />
                  </div>
                </div>
          })
        }
      </div>
      }
    </div>
  )
}

export default Errands