import '../css/errands.styles.css';

function Errand (props) {

	return (
		<>
		{Object.values(props.errands).map((errand, index) => {
			return <p 
						key={index}
						id={errand}
						value={errand}
						className={Number.parseInt(errand[0]) ? "errand-entry-date" : "errand-entry-task"}
						onClick={props.onClick}
					>
						{errand}
					</p>
		})}
		</>
	)
}

export default Errand