import '../css/budget/calculator.styles.css';
import calculator from '../assets/calculator.svg';
import deleteButt from '../assets/delete.svg';

function Calculator({
    calculation, 
    calculate, 
    showCalculator, 
    showCALC, 
    dividend, 
    divisor, 
    operator, 
    deleteCalculate
}) {

  return (
    <div id="to-calculate">
        <div id="show-calc-button">
            <img 
                src={calculator}  
                id="calc-button" 
                onClick={showCALC} 
                alt="calculator icon"
            /> 
        </div>
        {showCalculator &&
            <div id="calculator">
                <div id="calculation-display">
                    <div id="dividend" className="equation-component">
                        {dividend !== undefined && 
                        <p>{dividend}</p>
                        }
                    </div>
                    <div id="operator" className="equation-component">
                        {operator !== undefined &&
                        <p>{operator}</p>
                        }
                    </div>
                    <div id="divisor" className="equation-component">
                        {divisor != 0 && divisor !== undefined &&
                        <p>{divisor}</p>
                        }
                    </div>
                    <div id="calculation" className="equation-component">
                        {calculation !== undefined &&
                        <p>{calculation}</p>}
                    </div>
                </div>
                <div id="buttons">
                    <div className="button-row">
                        <button id="AC" className="button" value="clear" onClick={calculate}>AC</button>
                        <button id="()" className="button" value="()" onClick={calculate}>( )</button>
                        <button id="%" className="button operator" value="%" onClick={calculate}>%</button>
                        <button id="/" className="button operator" value="/" onClick={calculate}>&#247;</button>
                    </div>
                    <div className="button-row">
                        <button id="7" className="button" value="7" onClick={calculate}>7</button>
                        <button id="8" className="button" value="8" onClick={calculate}>8</button>
                        <button id="9" className="button" value="9" onClick={calculate}>9</button>
                        <button id="*" className="button operator" value="*" onClick={calculate}>x</button>
                    </div>
                    <div className="button-row">
                        <button id="4" className="button" value="4" onClick={calculate}>4</button>
                        <button id="5" className="button" value="5" onClick={calculate}>5</button>
                        <button id="6" className="button" value="6" onClick={calculate}>6</button>
                        <button id="-" className="button operator" value="-" onClick={calculate}>-</button>
                    </div>
                    <div className="button-row">
                        <button id="1" className="button" value="1" onClick={calculate}>1</button>
                        <button id="2" className="button" value="2" onClick={calculate}>2</button>
                        <button id="3" className="button" value="3" onClick={calculate}>3</button>
                        <button id="+" className="button operator" value="+" onClick={calculate}>+</button>
                    </div>
                    <div className="button-row">
                        <button id="0" className="button" value="0" onClick={calculate}>0</button>
                        <button id="." className="button" value="." onClick={calculate}>.</button>
                        <button id="delete" className="button" onClick={deleteCalculate}><img src={deleteButt}/></button>
                        <button id="=" className="button" value="=" onClick={calculate}>=</button>
                    </div>
                </div>
            </div>
        }
    </div>
  )
}

export default Calculator